body {
  background-color: #121212;
  color: #fff;
  font-size: 18px;

  padding: 0;
  height: 88vh;
  max-width: 992px;
  margin: 0 auto;
}

.app {
  display: grid;
  height: 88vh;
  grid-template-rows: auto max-content;
  margin-left: 15px;
  margin-right: 15px;
}

.screen {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 50px;
  height: 100%;
}

.calc-body {
  display: grid;
  align-content: end;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, max-content);
  grid-row-gap: 15px;
  grid-column-gap: 15px;
  grid-template-areas:
    "btn1 btn2 btn3 btn4"
    "btn5 btn6 btn7 btn8"
    "btn9 btn10 btn11 btn12"
    "btn13 btn14 btn15 btn16"
    "span2 span2 btn17 btn18";
}

.span2 {
  grid-area: span2;
}

.btn {
  font-size: 30px;
  background-color: #323232;
  border: 0.1px solid #707070;
  padding: 12px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  border-radius: 15px;
  cursor: pointer;
}

.flash {
  animation-name: flash;
  animation-duration: 0.3s;
}

@keyframes flash {
  0% {
    background-color: #323232;
  }
  50% {
    background-color: #828282;
  }

  100% {
    background-color: #323232;
  }
}
